.weather-card {
  margin: 20px;
  background: linear-gradient(#f574f1, #04abf3);
  width: 600px;
  height: 600px;
  padding: 20px;
  color: black;
}
.C,
.F {
  margin: 30px;
  width: 50px;
  height: 50px;
  background-color: rgb(34, 12, 1);
  color: white;
}
.temp {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.icons {
  width: 100px;
  height: 100px;
}
